@import url(https://fonts.googleapis.com/css2?family=Kumbh+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {

  font-family: 'Kumbh Sans';
  text-align: justify;
}

html, body {
  height: 100%;
}

.menu {

  height: 100%;
  overflow: hidden;
  position: fixed;
  scroll-behavior: none;
  top: 0;
  z-index: 1;
}

.icon {
  color: #adadad;
  text-decoration: none;
}

.logo{

  border-radius: 50%;
  float: left;
  height: auto;
  margin: 0 2% 2% 0;
  width: 200px;
}

.logo-titre {

  font-size: larger;
  padding: 15% 5% 0 5%;
  line-height: 150%;
  text-align: center;
}

.logo-titre-qualif {

  font-size: large;
  text-align: center;
}

i {

  font-size: x-large;
}

.content-icon {

  margin: 1% 10%;
  width: 5%;
}


.footer-info {

  margin: 5%;
}

.footer-icon {

  margin: 1%;
}

.footer-icon:hover {

  color: white;
  cursor: pointer;
}
.mobile {

    width: 100%;
}

.mobile-menu {

    background-color: #1d1d1d;
    color: #adadad;
    padding-top: 10%;

}



.mobile-contact-info {

    text-align: right;
    padding: 2% 0 3% 5%;
}

.mobile-footer-icon {

    font-size: medium;
    margin: 1%;
    
}

.mobile-footer-icon:hover {

    color: white;
}

.mobile-bouton-menu {

    display: inline-block;
    position: absolute;
    left: 5%;
}

.dropdown-link, .mobile-bouton {

    background-color: #1d1d1d;
    border: none;
    color: #adadad;
}

.dropdown-link:hover, .mobile-bouton:hover {

    outline: none;
}

.content-display {

    padding: 5%;
    float: right;
    width: 80%;
}

@media screen and (max-width: 600px) {
    .content-display {
        width: 100%;
    }
}

.course-div {

    background-color: #f5f6fa;
    box-shadow: 0 0 5px grey;
    margin: 5%;
    padding: 2%;
}


.colone1 {

    text-align: right;
}


.project-div {

    margin: 5%;
    box-shadow: 0 0 5px grey;
}

.card-logo {

    padding: 2%;
}

.btn-primary, .btn-info {

    margin: 1%;
}

/* For all pages */
.all-page-container {
    height: 100%;
    max-height: 500px;
    overflow: auto;
}
